// ============= PREMIUM VOICE ASSISTANT =============
const VoiceAssistant = ({ onClose }) => {
  const [assistantState, setAssistantState] = useState('idle');
  const [isConnected, setIsConnected] = useState(false);
  const [isAutoMode, setIsAutoMode] = useState(false);
  const [interimTranscript, setInterimTranscript] = useState('');
  const [showHelp, setShowHelp] = useState(true);
  const [isVisible, setIsVisible] = useState(true);
  
  const recognitionRef = useRef(null);
  const synthRef = useRef(null);
  const silenceTimeoutRef = useRef(null);
  const isProcessingRef = useRef(false);
  const finalTranscriptRef = useRef('');
  const audioContextRef = useRef(null);
  const autoModeRef = useRef(false);

  useEffect(() => {
    autoModeRef.current = isAutoMode;
  }, [isAutoMode]);

  useEffect(() => {
    const timer = setTimeout(() => setShowHelp(false), 6000);
    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    const checkServices = () => {
      const speechSupported = 'webkitSpeechRecognition' in window || 'SpeechRecognition' in window;
      const puterReady = window.puter && window.puter.ai;
      const synthSupported = 'speechSynthesis' in window;
      
      const connected = speechSupported && puterReady && synthSupported;
      setIsConnected(connected);
    };
    
    checkServices();
    const interval = setInterval(checkServices, 3000);
    return () => clearInterval(interval);
  }, []);

  const playAudioCue = useCallback((type) => {
    if (!window.AudioContext && !window.webkitAudioContext) return; 
    
    try {
      const audioContext = audioContextRef.current || new (window.AudioContext || window.webkitAudioContext)();
      audioContextRef.current = audioContext;
      
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);
      
      switch (type) {
        case 'start':
          oscillator.frequency.setValueAtTime(800, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(1200, audioContext.currentTime + 0.1);
          break;
        case 'end':
          oscillator.frequency.setValueAtTime(1200, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(700, audioContext.currentTime + 0.15);
          break;
        case 'error':
          oscillator.frequency.setValueAtTime(400, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(200, audioContext.currentTime + 0.3);
          break;
        default:
          return;
      }
      
      gainNode.gain.setValueAtTime(0.08, audioContext.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.2);
      
      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.2);
    } catch (error) {
      console.warn('Audio cue failed:', error);
    }
  }, []);

  const extractContent = useCallback((obj, depth = 0) => {
    if (depth > 5) return null;
    if (typeof obj === 'string' && obj.trim()) {
      return obj.trim();
    }
    if (!obj) return null;
    
    if (obj.result && obj.result.message && obj.result.message.content) {
      return obj.result.message.content.trim();
    }
    
    const patterns = [
      'content',
      'message.content', 
      'result.message.content',
      'choices[0].message.content',
      'text',
      'response',
      'answer',
      'reply'
    ];
    
    for (const pattern of patterns) {
      try {
        const value = pattern.split('.').reduce((acc, key) => {
          if (key.includes('[')) {
            const [arrayKey, index] = key.split('[');
            const idx = parseInt(index.replace(']', ''));
            return acc?.[arrayKey]?.[idx];
          }
          return acc?.[key];
        }, obj);
        
        if (value && typeof value === 'string' && value.trim()) {
          return value.trim();
        }
      } catch (e) {
        // Continue to next pattern
      }
    }
    
    for (const key in obj) {
      if (obj.hasOwnProperty(key)) {
        const extracted = extractContent(obj[key], depth + 1);
        if (extracted) return extracted;
      }
    }
    
    return null;
  }, []);

  const getVoiceFallback = useCallback((input) => {
    const voiceResponses = {
      greeting: "Hey! I'm Laxman's AI assistant. What would you like to know?",
      identity: "Laxman is a software engineer at CAMS in Chennai, graduated from IIT Delhi.",
      projects: "He's built amazing AI projects! His main one processes 80,000+ documents monthly.",
      skills: "He's expert in Java, Python, JavaScript, and AI technologies.",
      contact: "Email him at laxman kethavath 5 at gmail dot com, or find him on LinkedIn.",
      location: "He's currently working in Chennai, India.",
      education: "He completed his dual degree in CS from IIT Delhi, 2018 to 2024.",
      help: "Ask me about Laxman's work, projects, skills, or contact info!",
      default: "Could you ask that differently? I'm here to help you learn about Laxman!"
    };

    const input_lower = input.toLowerCase();
    if (input_lower.includes('hello') || input_lower.includes('hi') || input_lower.includes('hey')) return voiceResponses.greeting;
    if (input_lower.includes('who') || input_lower.includes('about') || input_lower.includes('tell me')) return voiceResponses.identity;
    if (input_lower.includes('project') || input_lower.includes('work') || input_lower.includes('built')) return voiceResponses.projects;
    if (input_lower.includes('skill') || input_lower.includes('technology') || input_lower.includes('programming')) return voiceResponses.skills;
    if (input_lower.includes('contact') || input_lower.includes('reach') || input_lower.includes('email')) return voiceResponses.contact;
    if (input_lower.includes('where') || input_lower.includes('location') || input_lower.includes('live')) return voiceResponses.location;
    if (input_lower.includes('education') || input_lower.includes('study') || input_lower.includes('college')) return voiceResponses.education;
    if (input_lower.includes('help') || input_lower.includes('what can') || input_lower.includes('how to')) return voiceResponses.help;
    
    return voiceResponses.default;
  }, []);

  const getAIResponse = useCallback(async (userInput) => {
    try {
      const contextPrompt = `You are Laxman's voice assistant. Respond naturally and conversationally:

${LAXMAN_PORTFOLIO_DATA}

Guidelines:
- Keep responses under 25 words for voice clarity
- Be conversational and engaging
- Use "he" when referring to Laxman
- If unclear, ask brief follow-up questions

User said: "${userInput}"

Respond conversationally:`

      const aiResponse = await window.puter.ai.chat(contextPrompt, {
        model: 'gpt-4o-mini',
        max_tokens: 80,
        temperature: 0.9
      });

      const extractedContent = extractContent(aiResponse);
      return extractedContent || getVoiceFallback(userInput);
    } catch (error) {
      console.error('AI Error:', error);
      return getVoiceFallback(userInput);
    }
  }, [extractContent, getVoiceFallback]);

  const stopAll = useCallback(() => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
    }
    window.speechSynthesis.cancel();
    
    if (silenceTimeoutRef.current) {
      clearTimeout(silenceTimeoutRef.current);
    }
    
    setAssistantState('idle');
    setInterimTranscript('');
    isProcessingRef.current = false;
    finalTranscriptRef.current = '';
  }, []);

  const startListening = useCallback(() => {
    if (!isConnected || assistantState === 'listening' || isProcessingRef.current) return;

    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    const recognition = new SpeechRecognition();
    recognitionRef.current = recognition;

    recognition.continuous = true;
    recognition.interimResults = true;
    recognition.lang = 'en-US';
    recognition.maxAlternatives = 1;

    setAssistantState('listening');
    setInterimTranscript('');
    finalTranscriptRef.current = '';
    setShowHelp(false);
    
    playAudioCue('start');

    recognition.onresult = (event) => {
      let interimText = '';

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        
        if (event.results[i].isFinal) {
          finalTranscriptRef.current += transcript;
        } else {
          interimText += transcript;
        }
      }

      if (interimText) {
        setInterimTranscript(interimText);
      }

      if (silenceTimeoutRef.current) {
        clearTimeout(silenceTimeoutRef.current);
      }

      silenceTimeoutRef.current = setTimeout(() => {
        const finalResult = finalTranscriptRef.current.trim();
        if (finalResult && !isProcessingRef.current) {
          if (recognitionRef.current) {
            recognitionRef.current.stop();
          }
          processFinalSpeech(finalResult);
        }
      }, 2000);
    };

    recognition.onerror = (event) => {
      console.error('Speech recognition error:', event.error);
      playAudioCue('error');
      
      if (event.error === 'network') {
        setTimeout(() => {
          if (assistantState === 'listening') {
            startListening();
          }
        }, 1000);
        return;
      }
      
      setAssistantState('idle');
      setInterimTranscript('');
      isProcessingRef.current = false;
    };

    recognition.onend = () => {
      const remainingText = finalTranscriptRef.current.trim();
      if (remainingText && !isProcessingRef.current && assistantState === 'listening') {
        processFinalSpeech(remainingText);
      } else if (!isProcessingRef.current) {
        setAssistantState('idle');
        setInterimTranscript('');
      }
    };

    try {
      recognition.start();
    } catch (error) {
      console.error('Failed to start recognition:', error);
      setAssistantState('idle');
      playAudioCue('error');
    }
  }, [isConnected, assistantState, playAudioCue]);

  const speakResponse = useCallback((text) => {
    if (!text || !text.trim()) {
      setAssistantState('idle');
      return;
    }
    
    if (!('speechSynthesis' in window)) {
      setAssistantState('idle');
      return;
    }

    const cleanText = text.trim();
    window.speechSynthesis.cancel();

    setTimeout(() => {
      const utterance = new SpeechSynthesisUtterance(cleanText);
      synthRef.current = utterance;

      utterance.rate = 0.9;
      utterance.pitch = 1.0;
      utterance.volume = 0.8;
      utterance.lang = 'en-US';

      utterance.onend = () => {
        setAssistantState('idle');
        
        if (autoModeRef.current) {
          setTimeout(() => {
            if (autoModeRef.current && assistantState !== 'listening') {
              startListening();
            }
          }, 1000);
        }
      };

      utterance.onerror = (event) => {
        console.error('Speech synthesis error:', event.error);
        setAssistantState('idle');
      };

      const setVoice = () => {
        const voices = window.speechSynthesis.getVoices();
        if (voices.length > 0) {
          const preferredVoice = voices.find(voice => 
            voice.lang.includes('en-US')
          ) || voices.find(voice => 
            voice.lang.includes('en')
          ) || voices[0];
          
          if (preferredVoice) {
            utterance.voice = preferredVoice;
          }
        }
      };

      if (window.speechSynthesis.getVoices().length > 0) {
        setVoice();
        window.speechSynthesis.speak(utterance);
      } else {
        window.speechSynthesis.onvoiceschanged = () => {
          setVoice();
          window.speechSynthesis.speak(utterance);
        };
        
        setTimeout(() => {
          window.speechSynthesis.speak(utterance);
        }, 500);
      }
    }, 100);
  }, [startListening, assistantState]);

  const processFinalSpeech = useCallback(async (finalText) => {
    if (finalText.length < 3 || isProcessingRef.current) return;
    
    isProcessingRef.current = true;
    setInterimTranscript('');
    setShowHelp(false);
    
    playAudioCue('end');
    setAssistantState('thinking');
    
    try {
      const aiResponse = await getAIResponse(finalText);
      setAssistantState('speaking');
      
      if (aiResponse && aiResponse.trim()) {
        speakResponse(aiResponse);
      } else {
        console.error('No valid response to speak');
        setAssistantState('idle');
      }
    } catch (error) {
      console.error('Error processing speech:', error);
      setAssistantState('idle');
    }
    
    isProcessingRef.current = false;
  }, [getAIResponse, playAudioCue, speakResponse]);

  const toggleAutoMode = useCallback(() => {
    setIsAutoMode(prev => {
      const newState = !prev;
      
      if (newState && assistantState === 'idle') {
        setTimeout(() => startListening(), 500);
      } else if (!newState) {
        stopAll();
      }
      return newState;
    });
  }, [assistantState, startListening, stopAll]);

  const testTTS = useCallback(() => {
    const testText = "Hello! I'm Laxman's AI assistant. How can I help you today?";
    speakResponse(testText);
  }, [speakResponse]);

  const closeChat = useCallback(() => {
    stopAll();
    setIsVisible(false);
    if (onClose) {
      onClose();
    }
  }, [stopAll, onClose]);

  const getVoiceStatus = () => {
    switch (assistantState) {
      case 'listening':
        return {
          title: "I'm Listening...",
          subtitle: "Speak now, I'm all ears!"
        };
      case 'thinking':
        return {
          title: "Processing...",
          subtitle: "Getting your answer..."
        };
      case 'speaking':
        return {
          title: "Speaking...",
          subtitle: "Here's what I found"
        };
      default:
        return {
          title: "Laxman's AI Assistant",
          subtitle: isConnected ? "Tap the avatar to start!" : "Loading services..."
        };
    }
  };

  const statusText = getVoiceStatus();

  if (!isVisible) return null;

  return (
    <VoiceContainer state={assistantState}>
      <ConnectionIndicator connected={isConnected} />
      <CloseButton onClick={closeChat}></CloseButton>

      <VoiceAvatar 
        state={assistantState}
        onClick={() => {
          if (assistantState === 'idle') {
            if (!window.speechSynthesis.speaking) {
              const testUtterance = new SpeechSynthesisUtterance('');
              testUtterance.volume = 0;
              window.speechSynthesis.speak(testUtterance);
            }
            startListening();
          } else {
            stopAll();
          }
        }}
      >
        {assistantState === 'listening' ? '' : 
         assistantState === 'thinking' ? '' : 
         assistantState === 'speaking' ? '' : ''}
        
        {/* Attractive wave rings for idle state */}
        {assistantState === 'idle' && (
          <>
            <WaveRing delay={0} style={{ width: '80px', height: '80px' }} />
            <WaveRing delay={0.5} style={{ width: '90px', height: '90px' }} />
            <WaveRing delay={1} style={{ width: '100px', height: '100px' }} />
          </>
        )}
      </VoiceAvatar>

      <VoiceStatus>
        <div className="status-title">{statusText.title}</div>
        <div className="status-subtitle">{statusText.subtitle}</div>
      </VoiceStatus>

      <VoiceWaves state={assistantState}>
        {[...Array(6)].map((_, i) => (
          <div key={i} className="wave" />
        ))}
      </VoiceWaves>

      <InterimText show={!!interimTranscript}>
        "{interimTranscript}"
      </InterimText>

      <VoiceControls>
        <ButtonRow>
          <div style={{ textAlign: 'center' }}>
            <VoiceButton
              onClick={assistantState === 'idle' ? startListening : stopAll}
              disabled={!isConnected}
              title={assistantState === 'idle' ? 'Start talking' : 'Stop'}
            >
              {assistantState === 'idle' ? '' : ''}
            </VoiceButton>
            <ButtonLabel>
              {assistantState === 'idle' ? 'One Time\nQuestion' : 'Stop'}
            </ButtonLabel>
          </div>

          <div style={{ textAlign: 'center' }}>
            <VoiceButton
              active={isAutoMode}
              onClick={toggleAutoMode}
              disabled={!isConnected}
              title="Continuous conversation mode"
            >
              
            </VoiceButton>
            <ButtonLabel>
              Keep\nListening
            </ButtonLabel>
          </div>

          <div style={{ textAlign: 'center' }}>
            <VoiceButton
              onClick={testTTS}
              title="Test voice output"
            >
              
            </VoiceButton>
            <ButtonLabel>
              Test\nVoice
            </ButtonLabel>
          </div>
        </ButtonRow>
      </VoiceControls>

      <HelpHint show={showHelp && assistantState === 'idle'}> 
         Say "Hello" or ask about Laxman's experience!
      </HelpHint>
    </VoiceContainer>
  );
};

const Siri = ({ onClose }) => {
  return <VoiceAssistant onClose={onClose} />;
};

export default Siri;